    margin: 0;
    padding: 0
}

button, input, select, textarea {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit
}

ol, ul {
    list-style: none
}

input::-ms-clear, input::-ms-reveal {
    display: none
}

::-moz-selection {
    background: #108ee9;
    color: #fff
}

::selection {
    background: #108ee9;
    color: #fff
}

