    color: rgba(0, 0, 0, .85);
    font-weight: 500
}

a {
    color: #108ee9;
    background: transparent;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    transition: color .3s ease
}

a:focus {
    text-decoration: underline;
    -webkit-text-decoration-skip: ink;
    text-decoration-skip: ink
}

a:hover {
    color: #49a9ee
}

a:active {
    color: #0e77ca
}

a:active, a:hover {
    outline: 0;
    text-decoration: none
}

a[disabled] {
    color: rgba(0, 0, 0, .25);
    cursor: not-allowed;
    pointer-events: none
}

.ant-divider {
    margin: 0 6px;
    display: inline-block;
    height: 8px;
    width: 1px;
    background: #ccc
}

code, kbd, pre, samp {
